import React, { useState, useEffect, useRef } from "react";
import { Card } from "primereact/card";
import { Row, Col, Container } from "react-bootstrap";
import { InputText } from "primereact/inputtext";
import { Button } from "primereact/button";
import { Toast } from "primereact/toast";
import { FloatLabel } from "primereact/floatlabel";
import { InputTextarea } from "primereact/inputtextarea";
import { ProgressSpinner } from "primereact/progressspinner";
import { useNavigate } from "react-router-dom";
import axios from "axios";

import "./contactUs.css";

export default function ContactUs() {
  const navigate = useNavigate();
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [contactNo, setContactNo] = useState("");
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);
  const toastCenter = useRef(null);
  const toast = useRef(null);

  const sendEmail = (ref) => {
    if (message == "" || message.length == 0) {
      alert("EMPTY");
    } else {
      setLoading(true);
      let enquireObj = {
        name: name,
        emailId: email,
        contactNo: contactNo,
        message: message,
      };

      //   axios.post("/contactUs", enquireObj).then((enquireResponse) => {
      //     console.log(enquireResponse, "++ OBJ");
      //     if (enquireResponse.data) {
      //       setLoading(false);
      //       toastCenter.current.show({
      //         severity: "success",
      //         summary: "Success",
      //         detail:
      //           "Thank you for reaching out! Your enquiry has been submitted successfully, and an email has been sent to us. We will get back to you as soon as possible.",
      //         life: 8000,
      //       });
      //     }
      //   });

      setLoading(false);
      ref.current.show({
        severity: "success",
        summary: "Success",
        detail:
          "Thank you for reaching out! Your enquiry has been submitted successfully, and an email has been sent to us. We will get back to you as soon as possible.",
        life: 8000,
      });
    }
  };

  const showMessage = (event, ref, severity) => {
    if (message == "" || message.length == 0) {
      ref.current.show({
        severity: "error",
        // summary: "Success",
        detail: "Please write your message.",
        life: 8000,
      });
    } else {
      setLoading(true);
      let enquireObj = {
        name: name,
        emailId: email,
        contactNo: contactNo,
        message: message,
      };

      axios.post("/contactUs", enquireObj).then((enquireResponse) => {
        if (enquireResponse.data) {
          setLoading(false);
          setName("");
          setEmail("");
          setContactNo("");
          setMessage("");

          toastCenter.current.show({
            severity: "success",
            summary: "Success",
            detail:
              // "Thank you for reaching out! Your enquiry has been submitted successfully, and an email has been sent to us. We will get back to you as soon as possible.",
              "Thank you for reaching out!. We will get back to you as soon as possible.",
            life: 8000,
          });
        }
      });
    }
  };

  return (
    <div className="sign-up-panel">
      <div className="contactUs-container">
        <Toast ref={toastCenter} position="center" />
        <div className="right-panel">
          <div className="title-section">
            <h5 className="contactUsLbl">Contact Us</h5>
            <FloatLabel className="contactUsFloating">
              <InputText
                // id="name"
                value={name}
                onChange={(e) => setName(e.target.value)}
                className="contactUsInputText"
              />
              <label htmlFor="name">Name</label>
            </FloatLabel>
            <br />
            <FloatLabel className="contactUsFloating">
              <InputText
                // id="emailId"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="contactUsInputText"
              />
              <label htmlFor="emailId">Email Id</label>
            </FloatLabel>
            <br />
            <FloatLabel className="contactUsFloating">
              <InputText
                // id="contactNo"
                value={contactNo}
                onChange={(e) => setContactNo(e.target.value)}
                className="contactUsInputText"
              />
              <label htmlFor="contactNo">Contact No</label>
            </FloatLabel>
            <br />
            <InputTextarea
              value={message}
              onChange={(e) => setMessage(e.target.value)}
              rows={3}
              cols={45}
              placeholder="Enter your message..."
            />
            <br />
            <center>
              {loading ? (
                <ProgressSpinner
                  style={{ width: "50px", height: "50px" }}
                  strokeWidth="8"
                  fill="var(--surface-ground)"
                  animationDuration=".5s"
                />
              ) : (
                <div>
                  <Button
                    label="Home"
                    // icon="pi pi-check"
                    className="contactUsSendBtn"
                    // onClick={(e) => showMessage(e, toastCenter, "error")}
                    onClick={() => navigate("/")}
                  />
                  <Button
                    label="Send"
                    icon="pi pi-check"
                    className="contactUsSendBtn"
                    onClick={(e) => showMessage(e, toastCenter, "error")}
                  />
                </div>
              )}

              <hr className="contactUsHrTag" />
              <a className="contactUsEmailLbl" href="mailto:info@sipsite.co.uk">
                (or)
                <br /> Email Us: info@sipsite.co.uk
              </a>
            </center>
          </div>
        </div>
      </div>
    </div>
  );
}
